'use strict'

const CHANGE_NAME = 'CHANGE_NAME';

export default {
    namespaced: true,
    state:{
        place: 'La Paz'
    },
    getters: {
        getName(state){
            return state.place;
        }
    },
    mutations: {
        CHANGE_NAME(state, payload){
            state.place = payload;
        }
    }
}