import api from '../../../api/juarez';

const GETTING_INFO      = 'GET_INFO',
      GET_INFO_SUCCESS  = 'GET_INFO_SUCCESS';

export default {
    namespaced: true,
    state:{
        loading: false,
        data: [],

    },
    getters: {
        loading(state) {
            return state.loading;
        },
        data(state) {
            return state.data;
        }
    },
    mutations: {
        [GETTING_INFO](state) {
            state.loading = true;
            state.data = [];
        },
        [GET_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.data = change;
        }
    },
    actions: {
        async getInfo({ commit }) {
            commit(GETTING_INFO);
            try {
              let response = await api.getKbrsInfo();
              commit(GET_INFO_SUCCESS, response.data);
              return response.data;
            } catch (error) {
              console.log(error);
            }
          },
    }
}