import api from '../../../api/lapaz';

const UPDATE_SUCCESS    = 'UPDATE_SUCCESS',
      GETTING_INFO      = 'GET_INFO',
      GET_INFO_SUCCESS  = 'GET_INFO_SUCCESS',
      UPDATE_PROCESS    = 'UPDATE_PROCESS',
      GETTING_BD_INFO      = 'GETTING_BD_INFO',
      GET_BD_INFO_SUCCESS  = 'GET_BD_INFO_SUCCESS';

export default {
    namespaced: true,
    state:{
        notification: 'Guardar',
        loading: false,
        flag: false,
        data: [],
        bd: []
    },
    getters: {
        getNotif(state){
            return state.notification;
        },
        loading(state) {
            return state.loading;
        },
        flag(state) {
            return state.flag;
        },
        data(state) {
            return state.data;
        },
        bd(state) {
            return state.bd;
        }
    },
    mutations: {
        [GETTING_INFO](state) {
            state.loading = true;
            state.data = [];
        },
        [GET_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.data = change;
        },
        [UPDATE_SUCCESS](state, change){
            state.flag = true;
            state.notification = change;
        },
        [UPDATE_PROCESS](state){
            state.notification = 'Guardar';
            state.flag = false;
        },
        [GETTING_BD_INFO](state) {
            state.loading = true;
            state.bd = [];
        },
        [GET_BD_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.bd = change;
        }
    },
    actions: {
        async editInfo({ commit }, payload) {
            commit(UPDATE_PROCESS);
            try {
              let response = await api.editInfo(payload.base, payload.dias_sucios, payload.extra_m3);
              commit(UPDATE_SUCCESS, 'Cambios guardados');
              return response.data;
            } catch (error) {
              console.log(error);
            }
        },
        async getInfo({ commit }) {
            commit(GETTING_INFO);
            try {
              let response = await api.getCorteInfo();
              commit(GET_INFO_SUCCESS, response.data);
              return response.data;
            } catch (error) {
              console.log(error);
            }
        },
        async getBDInfo({ commit }) {
            commit(GETTING_BD_INFO);
            try {
              let response = await api.getBDInfoCorte();
              commit(GET_BD_INFO_SUCCESS, response.data);
              return response.data;
            } catch (error) {
              console.log(error);
            }
        },
    }
}