import axios from "axios";

export default {
  getCorteInfo() {
    return axios.get("/lapaz/corte/calculator");
  },
  getBDInfoCorte() {
    return axios.get("/lapaz/corte");
  },
  editInfo(base, dias_sucios, extra_m3){
    return axios.put('/lapaz/admin/corte',{
        base: base,
        dias_sucios: dias_sucios,
        extra_m3: extra_m3
    });
  },
  getAlmacenInfo() {
    return axios.get("/lapaz/almacen/calculator");
  },
  getCediInfo() {
    return axios.get("/lapaz/cedi/calculator");
  },
  getChoferInfo() {
    return axios.get("/lapaz/chofer-local/calculator");
  },
  getMoldeoInfo() {
    return axios.get("/lapaz/moldeo/calculator");
  },
  getViguetaInfo() {
    return axios.get("/lapaz/vigueta/calculator");
  },
};